import React from 'react';
import {AppBar, Toolbar, Tabs, Tab, Typography, createMuiTheme, ThemeProvider, Grid, Box, BottomNavigation, BottomNavigationAction} from '@material-ui/core';
import {BrowserRouter as Router, Route, Switch, Link, Redirect } from 'react-router-dom';
import { SocialIcon } from 'react-social-icons';

import About from './About.js';
import Experience from './Experience.js';
import Projects from './Projects.js';
const theme = createMuiTheme({
  overrides: {
    MuiAppBar: {
      // Name of the rule
      colorPrimary: {
        // Some CSS
        color: '#B0B0B0',
        backgroundColor: 'black',
      },
    },
    MuiBottomNavigation:{
    	root: {
    		backgroundColor: 'black',
    		width: '100%',
       	}
    }
  },
});

theme.typography.h3 = {
  fontSize: '1.5rem',
  '@media (min-width:600px)': {
    fontSize: '1.5rem',
  },
  [theme.breakpoints.up('md')]: {
    fontSize: '3.0rem',
  },
  fontFamily: 'Helvetica'
};
//navigation bar of website
class Navigation extends React.Component {
	constructor(props){
		super(props)
		this.state = {
			value: 0,
		}
		this.handleChange = this.handleChange.bind(this);
	}
	handleChange(e, value){
		console.log(this.state.value)
		this.setState({value: value})
	}
	render() {
		return (
		<ThemeProvider theme={theme}>
			<Router exact path="/">
				<Redirect to="/about"/>
				<AppBar color='primary' position="sticky">
					<Toolbar>
						<Grid container>
							<Grid item xs={6}>
								<Typography variant="h3">
								[Ethan Hsu]
								</Typography>
							</Grid>
							<Grid item xs={6}>
								<Grid container justify="flex-end">
									<Tabs value={this.state.value} onChange={this.handleChange}>
										<Tab label="About" component={Link} to="/about"/>
										<Tab label="Experience" component={Link} to="/experience" />
										<Tab label="Projects" component={Link} to="/projects"/>
									</Tabs>
								</Grid>
							</Grid>
						</Grid>
					</Toolbar>
				</AppBar>
				<Box bgcolor="black" minHeight="100vh">
					<Switch>
						<Route path="/about" component={About}/>
						<Route path="/experience" component={Experience}/>
						<Route path="/projects" component={Projects}/>
					</Switch>
					<Box paddingBottom="2rem" paddingTop="2rem">
						<BottomNavigation>
							<BottomNavigationAction icon={<SocialIcon url="https://www.linkedin.com/in/ethan-hsu-a3a21711b/"/>}/>
							<BottomNavigationAction icon={<SocialIcon url="https://github.com/ethsu1"/>}/>
							<BottomNavigationAction icon={<SocialIcon url="mailto:ethan.hsu97@gmail.com" network="email"/>}/>
						</BottomNavigation>
					</Box>
				</Box>
			</Router>

		</ThemeProvider>
	)
	}
}

export default Navigation;