import React from 'react';
import {Grid, Typography, createMuiTheme, ThemeProvider, Box, Card, CardMedia, CardContent} from '@material-ui/core';
import { Reveal } from 'semantic-ui-react';
import 'semantic-ui-css/semantic.min.css';

const theme= createMuiTheme({
	overrides: {

		MuiCardMedia :{
			media: {
				backgroundColor: "white",
				height: "auto",
				maxHeight: "80vh",
				width: "auto",
				maxWidth: "80vh"
			}
		},
		MuiCardContent: {
			root: {
				height: "auto",
				maxHeight: "80vh",
				width: "auto",
				maxWidth: "80vh"
			}
		},
		MuiGrid: {
			"grid-xs-6": {
				flexBasis: 'auto',
			}
		},
		MuiTypography:{
	    	h4: {
	    		fontFamily: "Futura"
	    	},
	    	body1: {
	    		fontSize: "1rem",
	    		fontFamily: "Futura"
	    	},
	    	body2: {
	    		fontFamily: "Futura",
	    		fontWeight: "bold"
	    	}
		}
	}
})
const styles1 = {
	height: "auto",
	width: "auto",
	maxWidth: "500px"
};

class ExperienceItem extends React.Component {
	constructor(props){
		super(props);
	}

	render(){
		return (
		<ThemeProvider theme={theme}>
			<Box paddingTop="2rem" paddingBottom="2rem">
				<Grid container alignItems="center" justify="center">
					<Grid item xs={6} component={Card}>
						<div style={styles1}>
							<Reveal animated='move down'>
								<Reveal.Content visible>
									<CardMedia component="img" image={this.props.logo}/>
									<CardContent>
										<Typography variant="h4">{this.props.company}</Typography>
										<Typography variant="body1">{this.props.title}</Typography>
									</CardContent>
								</Reveal.Content>
								<Reveal.Content hidden>
										<CardContent>
											{this.props.description.map((item) =>
												(<Box paddingTop="1rem" paddingBottom="1rem">
													<Typography variant="body1">{item}</Typography>
												</Box>)
											)}
											<Box paddingTop="1rem" paddingBottom="1rem">
												<Typography variant="body2">{this.props.skills}</Typography>
											</Box>
										</CardContent>
								</Reveal.Content>
							</Reveal>
						</div>
					</Grid>
				</Grid>
			</Box>
		</ThemeProvider>
		)
	}

}

export default ExperienceItem;